<template>
  <div class="page-layout users">
     <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
      <h3><b>تعديل فصل</b></h3>
    </div>
    <div class="text-center text-danger my-2" v-show="isLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <b-form @submit.stop.prevent="onSubmit" v-show="!isLoading">
      <b-container class="bv-example-row">
        <b-row>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-name" label="اسم الفصل" label-for="admin-add-name">
              <b-form-input
                id="admin-add-name"
                name="admin-add-name"
                v-model="$v.form.name.$model"
                :state="validateState('name')"
                aria-describedby="input-add-name"
              ></b-form-input>
              <b-form-invalid-feedback id="input-add-name">
                اسم الفصل مطلوب
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-subject" label="اسم المادة" label-for="admin-add-subject">
              <b-form-input
                id="admin-add-subject"
                name="admin-add-subject"
                v-model="$v.form.subject.$model"
                :state="validateState('subject')"
                aria-describedby="input-add-subject"
              ></b-form-input>
              <b-form-invalid-feedback id="input-add-subject"  v-if='!$v.form.subject.required'>
                اسم المادة مطلوبة
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-teacher_id" label="المدرس" label-for="admin-add-teacher_id">
              <b-form-select
                id="admin-add-teacher_id"
                name="admin-add-teacher_id"
                v-model="$v.form.teacher_id.$model"
                :options="teachers"
                :state="validateState('teacher_id')"
                aria-describedby="input-add-teacher_id"
              ></b-form-select>
              <b-form-invalid-feedback id="input-add-teacher_id" v-if='!$v.form.teacher_id.required'>المدرس مطلوب</b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-approvement" label="حالة الفصل" label-for="admin-add-approvement">
              <b-form-select
                id="admin-add-approvement"
                name="admin-add-approvement"
                v-model="$v.form.approvement.$model"
                :options="approvements"
                :state="validateState('approvement')"
                aria-describedby="input-add-approvement"
              ></b-form-select>
              <b-form-invalid-feedback id="input-add-approvement" v-if='!$v.form.approvement.required'>حالة الفصل مطلوبة</b-form-invalid-feedback>
            </b-form-group>
          </b-col>
        </b-row>
        <b-button type="submit" variant="primary">تعديل</b-button>
      </b-container>
      
    </b-form>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength, maxLength, alphaNum, email, numeric, sameAs, password} from "vuelidate/lib/validators";
import axios from 'axios';

export default {
  mixins: [validationMixin],
  data() {
    return {
      isLoading: false,
      alertCountdown: 0,
      alertType: '',
      alertText: '',
      approvements: [
        { value: null, text: "اختر..." },
        { value: 'accept', text: "مفتوح" },
        { value: 'under_revision', text: "تحت المراجعة" },
        { value: 'blocked', text: "مغلق" },
      ],
      form: {
        name: null,
        subject: null,
        approvement: null,
        teacher_id: null,
        block_reason: null
      },
      teachers: []
    };
  },
  validations: {
    form: {
      name: {
        required
      },
      subject: {
        required
      },
      teacher_id: {
        required
      },
      approvement: {
        required
      },
    }
  },
  methods: {
    validateState(name) {
      const { $dirty, $error } = this.$v.form[name];
      return $dirty ? !$error : null;
    },
    countDownChanged(alertCountdown) {
      this.alertCountdown = alertCountdown
    },
    onSubmit() {
      this.$v.form.$touch();
      if (this.$v.form.$anyError) {
        return;
      }
      this.isLoading = true;
      this.saveData();
    },
    saveData() {
      axios.put(process.env.VUE_APP_API_URL + '/admin/rooms/' + this.$route.params.id, this.form)
      .then((response) => {
        this.isLoading = false;
        this.alertType = "success"
        this.alertText = "تم تعديل بيانات الفصل بنجاح "
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      })
      .catch((error) => {
        this.isLoading = false;
        this.alertType = "danger"
        this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      });
    },
    loadData() {
      axios.get(process.env.VUE_APP_API_URL + '/admin/rooms/' + this.$route.params.id, this.form)
      .then((response) => {
        this.isLoading = false;
        this.form = response.data.data;
      })
      .catch((error) => {
        this.isLoading = false;
        this.alertType = "danger"
        this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        this.$router.push({
          name: 'students'
        })
      });
    },
    loadTeachers() {
      this.isLoading = true;
      this.teachers = [];
      axios.get(process.env.VUE_APP_API_URL + '/admin/teachers', {})
      .then((response) => {
        if(response.data.data.length > 0){
          response.data.data.forEach(element => {
            this.teachers.push({
              text: element.full_name,
              value: element.id
            })
          });
          this.loadData();
        }
      })
      .catch((error) => {
        this.isLoading = false;
        this.alertType = "danger"
        this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        this.$router.push({
          name: 'rooms'
        })
      });
    }
  },
  created() {
    this.isLoading = true;
    this.loadTeachers();
  }
};
</script>