<template>
  <div class="page-layout users">
     <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
      <h3><b>إضافة ملفات لفصل</b></h3>
    </div>
    <div class="text-center text-danger my-2" v-show="isLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <b-form @submit.stop.prevent="onSubmit" v-show="!isLoading">
      <b-container class="bv-example-row">
        <b-row>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-file" label="الملف" label-for="admin-add-file">
              <b-form-file
                type="file"
                id="admin-add-file"
                name="admin-add-file"
                v-model="$v.form.fileData.$model"
                :state="validateState('fileData')"
                aria-describedby="input-add-file"
              ></b-form-file>
              <b-form-invalid-feedback id="input-add-file">
                  الملف مطلوب
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-name" label="اسم الملف" label-for="admin-add-name">
              <b-form-input
                id="admin-add-name"
                name="admin-add-name"
                v-model="$v.form.name.$model"
                :state="validateState('name')"
                aria-describedby="input-add-name"
              ></b-form-input>
              <b-form-invalid-feedback id="input-add-name"  v-if='!$v.form.name.required'>
                اسم الملف مطلوبة
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-room_id" label="الفصل" label-for="admin-add-room_id">
              <b-form-select
                id="admin-add-room_id"
                name="admin-add-room_id"
                v-model="$v.form.room_id.$model"
                :options="rooms"
                :state="validateState('room_id')"
                aria-describedby="input-add-room_id"
              ></b-form-select>
              <b-form-invalid-feedback id="input-add-room_id" v-if='!$v.form.room_id.required'>الفصل مطلوب</b-form-invalid-feedback>
            </b-form-group>
          </b-col>
        </b-row>
        <b-button type="submit" variant="primary">اضافة ملف</b-button>
        <b-button class="mr-2" @click="resetForm()">اعادة</b-button>
      </b-container>
      
    </b-form>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength, maxLength, alphaNum, email, numeric, sameAs, password} from "vuelidate/lib/validators";
import axios from 'axios';
import { sendRequest }  from "../../../utils/RequestHelper.js"


export default {
  mixins: [validationMixin],
  data() {
    return {
      isLoading: false,
      alertCountdown: 0,
      alertType: '',
      alertText: '',
      form: {
        name: null,
        fileData: null,
        file: null,
        room_id: null,
      },
      rooms: []
    };
  },
  validations: {
    form: {
      name: {
        required
      },
      fileData: {
        required
      },
      room_id: {
        required
      },
    }
  },
  methods: {
    validateState(name) {
      const { $dirty, $error } = this.$v.form[name];
      return $dirty ? !$error : null;
    },
    countDownChanged(alertCountdown) {
      this.alertCountdown = alertCountdown
    },
    resetForm() {
      this.form = {
        name: null,
        file: null,
        fileData: null,
        room_id: null,
      };
      this.$nextTick(() => {
        this.$v.$reset();
      });
    },
    onSubmit() {
      this.$v.form.$touch();
      if (this.$v.form.$anyError) {
        return;
      }
      this.uploadFile();
    },
    uploadFile() {
      var formData = new FormData();
      formData.append("file", this.form.fileData);
      axios.post(process.env.VUE_APP_API_URL + '/admin/upload-file', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then((response) => {
        this.form.path = response.data;
        console.log(response.data);
        console.log(this.form);
        this.addData();
      })
      .catch((error) => {
        this.isLoading = false;
        this.alertType = "danger"
        this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      });
    },
    addData() {
      delete this.form.fileData;
      var successCallback = (response) => {
        this.isLoading = false;
        this.alertType = "success"
        this.alertText = "تم اضافة الملف بنجاح"
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      };
      var errorCallback = () => {
        this.isLoading = false;
        this.alertType = "danger"
        this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      };
      sendRequest('post', '/admin/filesrooms', false, this.form, successCallback, errorCallback);
    },
    loadRooms() {
      this.isLoading = true;
      this.rooms = [];
      var successCallback = (response) => {
        if(response.data.data.length > 0){
          response.data.data.forEach(element => {
            this.rooms.push({
              text: element.name,
              value: element.id
            })
          });
          this.isLoading = false;
        }
      };
      var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        this.$router.push({
          name: 'roomsFiles'
        })
      };
      sendRequest('get', '/admin/rooms', false, {}, successCallback, errorCallback);
    }
  },
  created() {
    this.loadRooms();
  }
};
</script>