import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'

// pages
import Home from '../views/Home.vue'
import NotFound from '../views/NotFound.vue'

// teachers
import Teachers from '../views/admin/teachers/Teachers.vue'
import AddTeacher from '../views/admin/teachers/AddTeacher.vue'
import EditTeacher from '../views/admin/teachers/EditTeacher.vue'

// students
import Students from '../views/admin/students/Students.vue'
import AddStudent from '../views/admin/students/AddStudent.vue'
import EditStudent from '../views/admin/students/EditStudent.vue'

// rooms
import Rooms from '../views/admin/rooms/Rooms.vue'
import AddRoom from '../views/admin/rooms/AddRoom.vue'
import EditRoom from '../views/admin/rooms/EditRoom.vue'

// roomsFiles
import RoomsFiles from '../views/admin/roomsFiles/RoomsFiles.vue'
import AddRoomFile from '../views/admin/roomsFiles/AddRoomFile.vue'
import EditRoomFile from '../views/admin/roomsFiles/EditRoomFile.vue'

// admins
import Admins from '../views/admin/admins/Admins.vue'
import AddAdmin from '../views/admin/admins/AddAdmin.vue'
import EditAdmin from '../views/admin/admins/EditAdmin.vue'

import routeAuth from "../utils/RoutingHelper.js"
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/teachers',
    name: 'teachers',
    component: Teachers,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/add-teacher',
    name: 'addTeacher',
    component: AddTeacher,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/edit-teacher/:id',
    name: 'editTeacher',
    component: EditTeacher,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/students',
    name: 'students',
    component: Students,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/add-student',
    name: 'addStudent',
    component: AddStudent,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/edit-student/:id',
    name: 'editStudent',
    component: EditStudent,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/rooms',
    name: 'rooms',
    component: Rooms,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/add-room',
    name: 'addRoom',
    component: AddRoom,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/edit-room/:id',
    name: 'editRoom',
    component: EditRoom,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/rooms/files',
    name: 'roomsFiles',
    component: RoomsFiles,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/rooms/add-file',
    name: 'addRoomFile',
    component: AddRoomFile,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/rooms/edit-file/:id',
    name: 'editRoomFile',
    component: EditRoomFile,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/admins',
    name: 'admins',
    component: Admins,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/add-admin',
    name: 'addAdmin',
    component: AddAdmin,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '/edit-admin/:id',
    name: 'editAdmin',
    component: EditAdmin,
    beforeEnter: routeAuth.isLoggedIn,
  },
  {
    path: '*',
    name: 'NotFound',
    component: NotFound,
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
